import React from "react";
import { animate, motion } from "framer-motion"

const variants = {

    initial1: {
        opacity: 0,
        y: 400,
    },
    animate1: {
        opacity: 1,
        y: 0,
        transition: {
            delay: .03,
        },
    },
}

export default function SirCaractText() {
    return (
        <div className="sc-test">
            <motion.section 
            className="sc-title test-title"
            variants={variants}
            initial="initial1"
            whileInView="animate1"
            viewport={{once: true}}
            >
                <span>Timpul pentru alt problem!</span>
                <p className="spec-title">// mai grea</p>
            </motion.section>
            <div className="enunt-container">
                <div className="enunt">
                    n fiierul <b>autor.in</b> se afl n aceast ordine urmtoarele date: <b>numele</b> i <b>prenumele</b> unui autor, <b>anul naterii</b>, <b>cartea de debut</b>, <b>anul debutului</b> i <b>numrul de critici literare</b> pe care le are cartea pn n prezent. Fiecare cuvnt sau cifr sunt separate prin caracterul #. S se afieze n fiierul <b>autor.out</b> irul scris astfel, cuvintele fiind separate prin spaiu: numele autorului, iniiala prenumelui autorului, anul debutului, carte de debut i anul naterii.
                    <p>Exemplu: n fiierul <i>autor.in</i> se afl <b>Creang#Ion#1837#Soacra cu trei nurori#1875#80</b> i se va afia n fiierul <i>autor.out</i> <b>Creang I 1875 Soacra cu trei nurori 1835</b>.</p>
                </div>
                <img src="./red-img.png" />
                
            </div>
            
        </div>
    )
}