import React, { useState } from "react";
import { animate, motion } from "framer-motion"

const variants = {

    initial1: {
        opacity: 0,
        x: -100,
    },
    animate1: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .40,
        },
    },

    initial2: {
        opacity: 0,
        x: -100,
    },
    animate2: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .50,
        },
    },

    initial3: {
        opacity: 0,
        x: -400,
    },
    animate3: {
        opacity: 1,
        x: 0,
        transition: {
            delay: .01,
        },
    },
}

export default function SirCaract(){

    const [hide1, setHide1] = useState(true);

    const handleClick1 = () => {
        setHide1(false)
    }

    const newTab = (url) => {
        const newW = window.open(url, '_blank', 'noopener,noreferrer')
        if (newW) newW.opener = null
    }

    let _btnClassName
    let _btnText
    if (hide1 === true) {
        _btnClassName = "btn1"
        _btnText = "Afieaz!"
    } else {
        _btnClassName = "btn1-rsp"
        _btnText = "O adres din memoria RAM specific unei variabile."
    }

    return (
        <motion.div
            className="sc"
            // variants={variants}
            // initial="initial1"
            // whileInView="animate1"
            // viewport={{once: true,}}
            >
                <motion.section 
                    className="sc-title"
                    variants={variants}
                    initial="initial1"
                    whileInView="animate1"
                    viewport={{once: true,}}
                    >
                        <span>char</span> iruri de caractere[];
                </motion.section>
                <motion.p 
                className="subtitle"
                variants={variants}
                initial="initial2"
                whileInView="animate2"
                viewport={{once: true,}}
                >
                    <p><img src='./question-mark.png' /><span>Ce sunt irurile de caractere?</span></p>
                    <p><img src='./hashtag.png' /><span>irurile de caractere sunt reprezentai de o serie de variabile de tip char, a crui punct de referin caracteristic este dat de adresa de memorie la care ncepe irul. <b>'\0'</b> se numte terminator i marcheaz finalul unui ir.</span></p>
                    <p><img src='terminal.png' /><span>Exemplu</span></p>
                    <section className="sc--code-sample">
                        <div className="sc--code-text">
                            <p>#include &lt;iostream&gt;</p>
                            <p></p>
                            <p></p>
                            <p>using namespace std;</p>
                            <p></p>
                            <p></p>
                            <p><span style={{color: "blue"}}>int</span> main()</p>
                            <p style={{marginLeft: 30}}><span style={{color: "blue"}}>char</span> s[];</p>
                            <p style={{marginLeft: 30}}><span style={{color: "blue"}}>int</span> n = 0;</p>
                            <p style={{marginLeft: 30}}>cin&gt;&gt;s; <span style={{fontStyle: 'italic', color: 'gray'}}> // citete un ir de caractere pn la primul caracter blank</span></p>
                            <p style={{marginLeft: 30}}>while (s[n] != '\0') n++; </p>
                            <p style={{marginLeft: 30}}>cout&lt;&lt;n; <span style={{fontStyle: 'italic', color: 'gray'}}> // afieaz lungimea irului</span></p>
                            <p></p>
                        </div>
                    </section> 
                    <p className="met"><img src='./hashtag.png' /><span>Pentru a economisi timp, se folosesc metode ce faciliteaz anumite operaii pe irurile de caractere. Pentru a le accesa, avem nevoie s declarm librria <b>cstring</b>.</span></p>
                    <ul>
                        <li>strlen(char*) - <u>return</u>: int, lungimea irului</li>
                        <li>strrev(char*) - <u>return</u>: *char (adresa la care ncepe irul), inverseaz irul</li>
                        <li>strupr(char*) - <u>return</u>: *char, transform literele mici n litere mari</li>
                        <li>strlwr(char*) - <u>return</u>: *char, transform literele mari n litere mici</li>
                        <li>strchr(char*, char) - <u>return</u>: *char, caut ntr-un ir, un caracter</li>
                        <li>strstr(char*, char*) - <u>return</u>: *char, caut ntr-un ir, alt ir</li>
                        <li>strspn(char*, char*) - <u>return</u>: int, caut ntr-un ir, alt ir i afieaz pe cte poziii se ntinde</li>
                        <li><b>strtok(char*, const char*) / strtok(NULL, const char*)</b> - este folosit pentru mprirea unui string mai mare n string-uri mai mici</li>
                    </ul>
                    <p><img src='terminal.png' /><span>Exemplu</span></p>
                    <section className="sc--code-sample2">
                        <div className="sc--code-text">
                            <p>#include &lt;iostream&gt;</p>
                            <p>#include &lt;cstring&gt;</p>
                            <p></p>
                            <p>using namespace std;</p>
                            <p></p>
                            <p></p>
                            <p><span style={{color: "blue"}}>int</span> main()</p>
                            <p style={{marginLeft: 30}}><span style={{color: "blue"}}>char</span> s[101];</p>
                            <p style={{marginLeft: 30}}>cin&gt;&gt;s;</p>
                            <p style={{marginLeft: 30}}>cout&lt;&lt;strlen(s); <span style={{fontStyle: 'italic', color: 'gray'}}> // afieaz lungimea irului</span></p>
                            <p></p>
                            <p><span style={{fontStyle: 'italic', color: 'gray'}}> // algoritm echivalent celui din primul exemplu</span></p>
                        </div>
                    </section> 
                    <p><img src='terminal.png' /><span>Exemplu <i>strtok</i></span></p>
                    <section className="sc--code-sample2">
                        <div className="sc--code-text">
                            <p>#include &lt;iostream&gt;</p>
                            <p>#include &lt;cstring&gt;</p>
                            <p></p>
                            <p>using namespace std;</p>
                            <p></p>
                            <p></p>
                            <p><span style={{color: "blue"}}>int</span> main()</p>
                            <p style={{marginLeft: 30}}><span style={{color: "blue"}}>char</span> s[101];</p>
                            <p style={{marginLeft: 30}}>cin.getline(s, 101);</p>
                            <p style={{marginLeft: 30}}><span style={{color: "blue"}}>char</span> *p = strtok(s, " ");</p>
                            <p style={{marginLeft: 30}}>for(; p; p=strtok(NULL, " ")) cout&lt;&lt;strrev(p);<span style={{fontStyle: 'italic', color: 'gray'}}> // afieaz irul fr spaii i fiecare cuvnt inversat</span></p>
                            <p></p>
                            <p></p>
                        </div>
                    </section> 
                    <p className="pop-quiz1"><img src="./lightning.png" /><span>Amintete-i!</span></p>
                    <p className="qa1"><img src="./question-mark.png" style={{marginLeft: 40}}/><span>Ce era un pointer?</span>
                        <motion.div 
                            className={_btnClassName} 
                            onClick={handleClick1}
                        >
                            {_btnText}
                        </motion.div>
                    </p>
                    <p className="qa1"><img src="./question-mark.png" style={{marginLeft: 40}}/><span>Alte exemple de metode din librria <b>cstring</b>?</span>
                        <motion.div 
                            className="btn1" 
                            onClick={() => newTab("https://cplusplus.com/reference/cstring/")}
                        >
                            C++ Docs
                        </motion.div>
                    </p>
                </motion.p>
        </motion.div>   
    )
}