import React from "react";
import { animate, motion } from "framer-motion"

const variants = {

    initial1: {
        opacity: 0,
        y: 400,
    },
    animate1: {
        opacity: 1,
        y: 0,
        transition: {
            delay: .03,
        },
    },
}

export default function SubprogrameTest() {
    return (
        <div className="sp-test">
            <motion.section 
            className="sp-title test-title"
            variants={variants}
            initial="initial1"
            whileInView="animate1"
            viewport={{once: true}}
            >
                <span>Problem solving TIME!</span>
            </motion.section>
            <div className="enunt-container">
                <img src="./study-online.png" />
                <div className="enunt">
                    n IDE-ul pe care l avei pus la dispoziie, creai un program C/C++ i realizai urmtoarele task-uri:
                    <ul>
                        <li>Creai un subprogram care s returneze 1, dac un numr este prim, sau 0 n caz contrar.</li>
                        <li>Creai un subprogram care s modifice in memorie un vector de numere ntregi, astfel nct numerele prime s fie pe poziii impare, iar cele neprime, pe poziii pare.</li>
                        <li><img src='./lightning.png' />Creai un subprogram prototip care s citeasc dintr-un fiier vector.in un ir de numere ntregi i s execute task-ul precedent. Ulterior, afiai pe ecran pentru fiecare numr neprim, cel mai apropiat numr prim.</li>
                        <li><img src='./question-mark.png' />Este programul vostru eficient? Argumentai ntr-un comentariu de tip extins la finalul programului.</li>
                    </ul>
                </div>
            </div>
            
        </div>
    )
}