import React from "react";
import { animate, motion } from "framer-motion"

const variants = {

    initial1: {
        opacity: 0,
        y: 400,
    },
    animate1: {
        opacity: 1,
        y: 0,
        transition: {
            delay: .03,
        },
    },
}

export default function StructText() {
    
    const newTab = (url) => {
        const newW = window.open(url, '_blank', 'noopener,noreferrer')
        if (newW) newW.opener = null
    }
    
    return (
        <div className="struct-test">
            <motion.section 
            className="struct-title test-title"
            variants={variants}
            initial="initial1"
            whileInView="animate1"
            viewport={{once: true}}
            >
                <span>Adaptare de problem!</span>
                <p className="spec-title">// grea</p>
            </motion.section>
            <div className="enunt-container">
                <div className="enunt">
                    n fiierul autor.in se afl n linii, fiecare coninnd n aceast ordine urmtoarele date: numele i prenumele unui autor, anul naterii, cartea de debut, anul debutului i numrul de critici literare pe care le are cartea pn n prezent. Fiecare cuvnt sau cifr sunt separate prin caracterul '#'. S se afieze n fiierul autor.out ordonai alfabetic dup titlul crii de debut, n aceast ordine: iniiala prenumelui autorului, numele autorului scris cu majuscule, secolul i deceniul n care s-a nscut separate prin caracterul '@', oglinditul anului de debut, titlul crii de debut n codul ASCII i cel mai apropiat numr prim de numrul de critici literare.
                    <p>Este sugerat aplicarea tuturor noiunilor recapitulate precedent i folosirea librriei <b>cstdlib</b>. -&gt; funcia <b>atoi(char*)</b> convertete un text n valoarea sa ntreag corespunztoare.</p>
                    <motion.p 
                            className="btn1" 
                            onClick={() => newTab("https://cplusplus.com/reference/cstdlib/")}
                        >
                            C++ Docs
                    </motion.p>
                </div>
                <img src="./purple-laptop.png" />
                
            </div>
            
        </div>
    )
}